import { authenticationAws } from "../modules/authenticationAws";
// import { authenticationSimulation } from "../modules/authenticationSimulation";
// import { authenticationSimulationDB } from "../modules/authenticationSimulationDB";

export async function handleSignInAttempt(
  event = new Event(),
  setErrorMessage,
  onSignIn
) {
  event.preventDefault();

  const inputs = event.target;
  const emailInput = inputs[1];
  const passwordInput = inputs[2];
  const closeButton = inputs[3];

  const email = emailInput.value;
  const password = passwordInput.value;

  // const isAuthenticated = authenticationSimulation(email, password);
  const resolveValue = await authenticationSimulationDB(email, password);
  // const resolveValue = await authenticationAws(email, password);
  const isAuthenticated = resolveValue;
  if (isAuthenticated) {
    closeButton.click();
    inputs.reset();
    onSignIn();
  } else setErrorMessage("The email and password don't match");
}
