import React from "react";
import { Link, NavLink } from "react-router";
import { SignInArea } from "./SignInArea";

export function Navbar() {
  debugger;
  let rootPath = "";
  const domain = window.location.hostname;
  if (domain === "rolazar.github.io") rootPath = "/react-navigation";

  return (
    <nav className="nav nav-tabs bg-dark">
      <NavLink className="nav-link" to={`${rootPath}/`} end>
        HOME
      </NavLink>
      <NavLink className="nav-link" to={`${rootPath}/gallery`}>
        GALLERY
      </NavLink>
      <NavLink className="nav-link" to={`${rootPath}/contact`}>
        CONTACT
      </NavLink>
      <SignInArea />
    </nav>
  );
}
