import { createRoot } from "react-dom/client";
import React from "react";
import { BrowserRouter, Route, Routes } from "react-router";
import { Home } from "./Views/Home";
import { Contact } from "./Views/Contact";
import { Gallery } from "./Views/Gallery";
import { Header } from "./Views/Header";
import { Footer } from "./Views/Footer";
import { HandleRefresh } from "./Views/HandleRefresh";
import { Custom404 } from "./Views/Custom404";

let rootPath = "";
const hostname = window.location.hostname;
if (hostname === "rolazar.github.io") rootPath = "/react-navigation";

const react = createRoot(window.bodyTag);
react.render(
  <BrowserRouter>
    <HandleRefresh>
      <Header />
      <Routes>
        <Route path={`${rootPath}/`} element={<Home />} />
        <Route path={`${rootPath}/home/main`} element={<Home />} />
        <Route path={`${rootPath}/contact`} element={<Contact />} />
        <Route path={`${rootPath}/gallery`} element={<Gallery />} />
        <Route path="*" element={<Custom404 />} />
      </Routes>
      <Footer />
    </HandleRefresh>
  </BrowserRouter>
);
